

header {
  background-color: #61dafb;
  display: flex;
  justify-content: space-between;
  height: 40px;
}

main {
  background-color: rgb(202, 202, 202);
  height: calc(100vh - 40px);
  width: 100%;
  display: flex;
}
.BarLeft {
width: 50%;
background-color: rgb(202, 202, 202);
height: 100%;
display: flex;
justify-content: center;
align-items: center;

}
.currentBar {
width: 80%;
height: 80%;
background-color: rgb(202, 202, 202);
overflow-y: auto;
position: relative;
display: flex;
flex-direction: column;
align-items: flex-start;
}

.Coin {

display: flex;
margin: 4px;
font-family: 'Courier New', Courier, monospace;
font-weight: 600;
align-items: center;
border-bottom:2px solid rgba(95, 95, 95, 0.685) ;
cursor: pointer;
}
.Coin_name {
  margin-right: 10px;
  font-size: 1.1em;
}
.Coin:hover {
box-shadow: 0px 0px 7px rgb(12, 14, 12);
}
.Coin span {
  margin:  0 4px;
  font-size: 1em;
}
.Coin_usd{
  color: rgb(60, 175, 80);
  font-weight: 800;
 
}
.Coin_vol {
  color: rgb(27, 27, 131);
}

.favorit {
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
}
.favorit img {
  cursor: pointer;
}


.BarRight {
  width: 50%;
  background-color: rgb(202, 202, 202);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.CurrentD {
  display: flex;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 600;
  flex-direction: column;
  align-items: center;
  width: calc(95% - 4px);
  justify-content: space-between;

  border-bottom: 3px solid rgba(102, 102, 102, 0.712);
  padding: 10px;
}
.CurrentD p:nth-child(1){
  color: rgb(63, 78, 66);
}
.CurrentD p:nth-child(2){
  color: rgb(7, 65, 26);
}
.CurrentD p:nth-child(3){
  color: rgb(87, 18, 18);
}
.CurrentD p:nth-child(4){
  color: rgb(7, 65, 26);
}
.CurrentPriceMiddlSum {
  border-bottom: 1px dashed rgb(0, 0, 0);
}
.CurrentName {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  font-size: 1.3em;
  width: 100%;
}
.CurrentName span:nth-child(1){

}
.CurrentName span:nth-child(2){
  font-size: 1.3em;
  /* box-shadow: 0px 0px 12px green; */
  border-radius: 20px;
  transform: translate(0,-6px);

}
.CurrentContPrice {

  width: 100%;
}


@media (max-width:1000px) {

header {
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 65px;
}
.Coin {
  justify-content: space-between;
}
main {
  height: calc(100vh - 65px);
  overflow-y: scroll;
  
  flex-direction: column;
}
.BarLeft , .BarRight {
  width: calc(100% - 4px);
  height: 50vh;
  border: 2px solid rgb(36, 36, 36);
}
.currentBar {
  width: 98%;
  height: 98%;
  overflow-x: hidden;
}
}